.recommended {

  &__section-title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__products {
    margin-bottom: 48px;

    @include pageGrid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 56px;
    }
  }

  &__product {
    overflow: hidden;
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: span 4;
    }
  }
}
