.page {
  color: $main-text-color;
  font-family: $font-family-first;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
